.hero{
    height: 120vh;
    background: linear-gradient(180deg, #fde1ff,#e1ffea22 60%);
    display: flex;
}
.hero-left{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap:20px;
    padding-left: 150px;
    padding-bottom: 250px;
    line-height: 1.1;
   
}
.hero-left h2{
    color: #090909;
    font-size: 26px;
    font-weight: 600;
  

  

}
.hero-left P{
    color: #171717;
    font-size: 30px;
    font-weight: 700;
    margin-top: -10px;
    


    

}
.hero-hand-icon{
    display: flex;
    align-items: center;
    
    
    
    

}
.hero-hand-icon img{
    width: 50px;
    margin-top: -15px;
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    
    width: 310px;
    height: 70px;
    border-radius:75px ;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;

}
.hero-right{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}